import { useEffect, useState } from 'react';
import { Data } from './Components/Data';
import * as XLSX from 'xlsx';
import './stly.css';
import 'bootstrap/dist/css/bootstrap.css';
import { Button } from '@mui/material';
import { makeStyles } from '@material-ui/core/styles';
import SaveIcon from '@material-ui/icons/Save';
import { requesicoes } from "../../../utils/services";
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';


import moment from 'moment';
import Swal from 'sweetalert2';

const useStyles = makeStyles(() => ({
    root: {
        '&:hover': {
            backgroundColor: 'transparent'
        }
    }
}));
let sexos = [];
let documentos = [];
let provincias = [];
let nacionalidades = [];
let distritos = [];
let cidades = []
let cursos = [];
let curriculos = [];
let nacionalId = {}

export default function Estudantes() {
    const classes = useStyles();
    // on change states
    const [excelFile, setExcelFile] = useState(null);
    const [excelFileError, setExcelFileError] = useState(null);
    const [hidden, setHidden] = useState(true);
    const [mostrarMessagemErro, setMostrarMessagemErro] = useState(true);
    const [mostrarMessagemSucesso, setMostrarMessagemSucesso] = useState(true);
    const [detalhe, setDetalhe] = useState(null);
    const [excelData, setExcelData] = useState(null);
    const [data, setData] = useState(null);
    const [updateDOM, setUpdateDOM] = useState(false);
    const [carregando, setCarregando] = useState(true);
    const [to, setTo] = useState('');
    const [redirect, setRedirect] = useState(false);
    const [loading, setLoading] = useState(false);


    useEffect(() => {

        init();
        setTimeout(() => {
            setCarregando(false);
        }, 2000);
    }, [])

    async function init() {
        setCarregando(true);

        const [requestSexo, tiposDocumentos, listaCurso, listaPaises, listaprovincias, listaCidades, curriculoRequest]
            = await Promise.all([requesicoes().buscarSexo(),
            requesicoes().buscarDocumentoEst(), requesicoes().buscarCurso('?campos=id,nome'), requesicoes().buscarPaises(), requesicoes().buscarProvincia('?campos=id,name,country_id.id'),
            requesicoes().buscarCidades('?campos=id,name,regiaoPai.id,regiaoPai.name'), requesicoes().buscarCurriculos()])

        // nacionalidade = listaPaises.data
        sexos = requestSexo.data
        documentos = tiposDocumentos.data
        provincias = listaprovincias.data
        distritos = listaCidades.data
        nacionalidades = listaPaises.data
        cursos = listaCurso.data
        curriculos = curriculoRequest.data
        setCarregando(false);

        setUpdateDOM(!updateDOM);

    }

    const fileType = ['application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'];
    const handleFile = (e) => {
        let selectedFile = e.target.files[0];

        if (selectedFile) {
            if (selectedFile && fileType.includes(selectedFile.type)) {
                let reader = new FileReader();
                reader.readAsArrayBuffer(selectedFile);
                reader.onload = (e) => {
                    setExcelFileError(null);
                    setExcelFile(e.target.result);
                };
            } else {
                setExcelFileError('Por favor selecione um ficheiro excel');
                setExcelFile(null);
            }
        } else {
            console.log('Por favor selecione um ficheiro');
        }
    };


    function removerEspacosNaoPadrao(texto) {
        return texto.replace(/\s+/g, ' ');
    }

    // submit function
    const handleSubmit = (e) => {
        setHidden(false);
        e.preventDefault();
        if (excelFile !== null) {
            const workbook = XLSX.read(excelFile, { type: 'buffer' });
            const worksheetName = workbook.SheetNames[0];
            const worksheet = workbook.Sheets[worksheetName];

            const data = XLSX.utils.sheet_to_json(worksheet).map(item => {
                // const regexPais = /Moc|bique/i;
                const prov = provincias.find(p => p.name === (item.naturalidade));
                const cid = distritos.find(c => c.name === (item.cidade));
                const cur = cursos.find(c => removerEspacosNaoPadrao(c.nome) == removerEspacosNaoPadrao(item.curso));

                if (item.curriculo !== undefined) {
                    const curri = curriculos?.find(c => c.ano === (item.curriculo));
                    const curriculo = curri ? curri : null
                    item.curriculo = curriculo


                } else {
                    setExcelFileError(`O ficheiro não contem curriculo para o curso ${item.nome}`)

                }

                const provincia = prov ? prov : null

                const cidade = cid ? cid : null
                const curso = cur ? cur : null

                const paisEncontrado = nacionalidades.find(n => removerEspacosNaoPadrao(n.name) === removerEspacosNaoPadrao(item.nacionalidade))

                nacionalId = paisEncontrado ? paisEncontrado : null;
                if (item.sexo === "F") {
                    item.sexo = 'Femenino'
                } if (item.sexo === "M") {
                    item.sexo = 'Masculino'
                }
                item.nacionalidade = nacionalId;
                item.naturalidade = provincia;
                item.cidade = cidade;
                item.curso = curso;

                if (item.dataNascimento !== undefined) {
                    const dataNasct = item.dataNascimento?.split('-');
                    item.dataNascimento = `${dataNasct[2]}-${dataNasct[1]}-${dataNasct[0]}`;
                }

                if (item.dataEmissao !== undefined) {
                    const dataEmis = item.dataEmissao?.split('-');
                    item.dataEmissao = `${dataEmis[2]}-${dataEmis[1]}-${dataEmis[0]}`;

                }
                // Modifique o valor do atributo desejado aqui

                if (item.incorporacao !== undefined) {

                    const dataIncorporacao = item.incorporacao?.split('-');
                    item.incorporacao = `${dataIncorporacao[2]}-${dataIncorporacao[1]}-${dataIncorporacao[0]}`;

                }
                // console.log('=============Data de Incorporacao-2: ', item.incorporacao);


                return item;
            });

            setData(data);

            //Depende do formato da data que vai receber do banco
            const dados = data.map((item) => {
                const regex = /Masculino|Feminino/i; // 'i' torna a busca case-insensitive (ignora maiúsculas/minúsculas)
                const sexoEncontrado = sexos.find(sexo => regex.test(sexo));
                const sexoId = sexoEncontrado ? sexoEncontrado : null;


                const prov = provincias.find(p => p.name === (item.naturalidade?.name));
                if (!prov) {
                    setExcelFileError(`Não encontrou Província do estudante ${item.nome}`)
                }
                const cid = distritos.find(c => c.name === (item.cidade?.name));
                if (!cid) {
                    setExcelFileError(`Não encontrou distrito do estudante ${item.nome}`)

                }
                const cur = cursos.find(c => c.nome === (item.curso?.nome));
                if (!cur) {
                    setExcelFileError(`Não encontrou Curso do estudante ${item.nome}`)

                }
                if (!item.curriculo) {
                    setExcelFileError(`Não encontrou Curriculo para o curso de ${item.curso.nome} `)

                }
                if (!item.dataNascimento) {
                    setExcelFileError('O campo data de Nascimento e obrigatorio: ' + item.nome); // Lança uma exceção
                }
                /*if (!item.dataEmissao) {
                    setExcelFileError('O campo data de Emissão e obrigatorio: ' + item.nome); // Lança uma exceção
                    setUpdateDOM(!updateDOM)
                }*/
                const provincia = prov ? prov : null
                const cidade = cid ? cid : null
                const curso = cur ? cur : null

                nacionalId = item.nacionalidade ? item.nacionalidade : null;
                const dataNasc = moment((item.dataNascimento), 'YYYY-MM-DD')

                return {
                    ...item,
                    Genero: sexoId,
                    nacionalidade: nacionalId?.name,
                    naturalidade: provincia?.name,
                    cidade: cidade?.name,
                    curriculo: item.curriculo.ano,
                    curso: curso?.nome,
                    dataNascimento: dataNasc.format('DD/MM/YYYY ')

                };
            });

            setExcelData(dados);
        } else {
            setExcelData(null);
        }
    };
    //Salvar no BD
    async function salvar(data) {

        // console.log('Dados', JSON.stringify(data));

        try {
            setLoading(true); // Ativa o spinner de carga
            await Promise.resolve(requesicoes().postEstudantes(data));

            Swal.fire(
                'Exito!',
                'Salvou com sucesso!',
                'success'
            )
            setTo('/inscrito/list');
            setRedirect(true);
        } catch (e) {
            if (e.response.status >= 500) {
                Swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: e.response.data.message,
                    footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                })
            } else if (e.response.status < 500) {
                Swal.fire({
                    icon: 'warning',
                    title: 'Oops...',
                    text: e.response.data.message,
                    // footer: 'Se o erro pressistir porfavor contacte o centro Informatico'
                })
            }
        } finally {
            setLoading(false); // Desativa o spinner de carga após a promise ser resolvida
        }
    }
    if (redirect)
        return (<Redirect to={to} />)
    return (
        <div>
            {carregando ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                    <MoonLoader color="#36d7b7" />
                </div>

            ) : (<div className="container">
                <div className="alert alert-danger" role="alert" hidden={mostrarMessagemErro}>
                    {detalhe}
                </div>
                <div className="alert alert-primary" role="alert" hidden={mostrarMessagemSucesso}>
                    Estudantes Salvo suceso
                </div>
                {/* Carregar Ficheiro */}
                <div className="form">
                    <form className="form-group" autoComplete="off" onSubmit={handleSubmit}>
                        <h5>Carregar Estudantes</h5>
                        <br></br>
                        <input type="file" className="form-control" onChange={handleFile} required></input>
                        {excelFileError && (
                            <div className="text-danger" style={{ marginTop: 5 + 'px' }}>
                                {excelFileError}
                            </div>
                        )}
                        <button className="btn btn-success" style={{ marginTop: 5 + 'px' }}>
                            Visualisar
                        </button>
                    </form>
                </div>

                <br></br>
                <hr></hr>

                {/* Vizualicao dos dados do ficheiro */}
                <h5>Lista de Pagamentos</h5>
                <div className="viewer">
                    {excelData === null && <>Nenhum conteudo selecionado</>}
                    {excelData !== null && (
                        <div className="table-responsive">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th scope="col">Número</th>
                                        <th scope="col">Apelido</th>
                                        <th scope="col">Nome</th>
                                        <th scope="col">Filiação (Nome do Pai)</th>
                                        <th scope="col">Filiação (Nome da Mãe)</th>
                                        <th scope="col">Genero</th>
                                        <th scope="col">Data Nascimento</th>
                                        <th scope="col">Tipo de Documento</th>
                                        <th scope="col">Número do Documeto</th>
                                        <th scope="col">Data Emissao</th>
                                        <th scope="col">Nuit</th>
                                        <th scope="col">Telemovel</th>
                                        <th scope="col">Email</th>
                                        <th scope="col">Nacionalidade</th>
                                        <th scope="col">Província</th>
                                        <th scope="col">Distrito</th>
                                        <th scope="col">Curso</th>
                                        <th scope="col">Curriculo</th>
                                        <th scope="col">Tipo de disposetivo Electronico</th>
                                        <th scope="col">Data de Incorporacao</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <Data excelData={excelData} />
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>
                {loading &&
                    <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
                        <MoonLoader color="#36d7b7" />
                    </div>
                }
                {!excelFileError && (<Button
                    variant="contained"
                    color="primary"
                    type="submit"
                    size="large"
                    onClick={() => {
                        salvar(data);
                    }}
                    className={classes.button}
                    startIcon={<SaveIcon />}
                    style={{ float: 'right' }}
                    hidden={hidden}
                >
                    Salvar
                </Button>)}

            </div>)}
        </div>
    );
}
